.title {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  color: #182135;
}
.titleOrange {
  color: #fc800d;
}
.text {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 18px;
  color: #626264;
}
.btn {
  background: #503e9d;
  border-radius: 10px;
  width: 176px;
  height: 48px;
}

.img {
  position: relative;
}
.layre1,
.layre2,
.layre2 {
  position: absolute;
}
.layre1 {
  z-index: 1;
  top: 40px;
}
.layre2 {
  z-index: 2;
  top: 30px;
}
.layre3 {
  z-index: 3;
}

.wrapper {
  position: relative;
}

.wrapContent {
  position: absolute;
}
